<script lang="ts">;
;
import { createEventDispatcher } from "svelte";
import Task from "./Task.svelte";
const dispatch = createEventDispatcher();
export let current;
export let taskList;
function moveLeft(task) {
    taskList.remove(task);
    dispatch("left", task);
}
function moveRight(task) {
    taskList.remove(task);
    dispatch("right", task);
}
</script>

<style lang="less">task-list {
  display: block;
  padding: 5px;
}
task-list:empty {
  display: none;
}
</style>

<task-list>
    {#each $taskList as task (task.id)}
        <Task
                {current}
                title={task.title}
                on:changed={event => taskList.updateTask(task, event.detail)}
                on:close={() => taskList.remove(task)}
                on:moveDown={() => taskList.moveDown(task)}
                on:moveBottom={() => taskList.moveBottom(task)}
                on:moveUp={() => taskList.moveUp(task)}
                on:moveTop={() => taskList.moveTop(task)}
                on:moveLeft={() => moveLeft(task)}
                on:moveRight={() => moveRight(task)}
        />
    {/each}
</task-list>
